import { AppWrapper } from 'AppWrapper';
import React, { lazy, Suspense } from 'react';
import { BrowserView, MobileView } from 'react-device-detect';

/* todo 

entering animation

*/

function App() {
	const ForMobile = lazy(() => import('containers/ForMobile'));
	const ForPC = lazy(() => import('containers/ForPC'));

	return (
		<AppWrapper>
			<Suspense fallback={<div>Loading...</div>}>
				<BrowserView>
					<ForPC />
				</BrowserView>
				<MobileView>
					<ForMobile />
				</MobileView>
			</Suspense>
		</AppWrapper>
	);
}

export default App;
